	module.exports = {"data":{"markdownRemark":{"html":"<p>授業では、中国の政治発展の傾向をより深く理解するために、比較政治発展の問題について議論し、中国の政治発展と中国社会、西洋社会、東部社会の歴史的手がかりを比較する。私は、歴史のなかで自分の立場を確立することに注意を払い、問題をより深く学ぶことができると言いました。私たちはすべて歴史から来ており、誰もが歴史を超えて問題を見ることはできません。しかし、時には、少しの歴史を超越して、自分の思考の深遠さに意味があることもあります。 19世紀以来、マルクスの東洋文明への侵略の歴史的時代に入り、社会が西洋になったことを知ったときに参考システムを作り出している。これは私たちが取り除くことができるものではなく、歴史的発展の沈殿物です。私たちはこの世界に住み、それを受け取ります。本当の画期的なのは、それらを超えて進むことです。西洋文明はアジアの小さな文明の影響を受けていないのですか？それで、何百年もかかることがあります。</p>\n<p>\"指をはじく\"、そして実際に科学と理性から問題を分析する。このようにして、中国はそれ自体をより完全に理解するでしょう。</p>\n<p>別のポイントは、論理分析の問題です。博士課程の学生は、「中国の政治哲学は人と自然、社会と宇宙との間の不調和を認識し、公的から私的である」と言って、中西欧の政治哲学の基本精神を大胆に構想した。合理的で西洋的な政治哲学は、人と自然、社会と宇宙との調和が調和していないことを認識し、私的なものから公共のものへと変化しています。問題の要点は、一般化する能力を持っていなければならず、第二のものは論理的な投機的な議論能力がなければならないということです。そのような命題は、基本的に論じる必要のある論理である。「公的から私的へ」、私的から公的へ「誰が合理的なのか？論理的な説明があり、現実的な議論が必要である。今や全体的な傾向は「民間から公共へ」と思われる。これは西洋の人と世界の理解の産物であるといえる。東は異なっており、理想的な人生とは異なっています。問題はより良い論理的議論が必要であるということです。</p>","id":"/home/swain/workspace/political-life/src/pages/ja/0173.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/ja/0173.md","frontmatter":{"title":"10月31日 月曜日"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/ja/0173.md absPath of file >>> MarkdownRemark"}}

